
import { defineComponent, PropType, toRefs } from 'vue';
import { CssClass } from '@/helpers/css-classes';
import {
  linkProps,
  useLink
} from '@/composition-functions';

export default defineComponent({
  name: 'VToolbarMenuItem',
  props: {
    active: {
      type: Boolean as PropType<boolean>,
      default: false
    },
    ...linkProps
  },
  emits: {
    click: null
  },
  setup (props) {
    const {
      to,
      href,
      newWindow
    } = toRefs(props);

    const {
      component,
      target
    } = useLink(to, href, newWindow);

    return {
      component,
      target
    };
  },
  computed: {
    classes (): CssClass[] {
      return [...this.$bem({})];
    }
  },
  methods: {
    onClick (): void {
      this.$emit('click');
    }
  }
});
