
import { defineComponent, PropType, toRefs } from 'vue';
import { VWrapper } from '@/components/Wrapper';
import {
  bgColorClass,
  CssClass
} from '@/helpers/css-classes';
import {
  themeProps,
  useTheme
} from '@/composition-functions';

export default defineComponent({
  name: 'VToolbar',
  components: {
    VWrapper
  },
  props: {
    fixed: {
      type: Boolean as PropType<boolean>,
      default: false
    },
    absolute: {
      type: Boolean as PropType<boolean>,
      default: false
    },
    color: {
      type: String,
      default: 'default'
    },
    ...themeProps
  },
  setup (props) {
    const {
      dark,
      light
    } = toRefs(props);

    return {
      themeClass: useTheme(dark, light)
    };
  },
  computed: {
    classes (): CssClass[] {
      const colorClasses = this.color ? [bgColorClass(this.color)] : [];
      return [
        ...this.$bem({
          m: {
            fixed: this.fixed,
            absolute: this.absolute
          }
        }),
        ...colorClasses,
        this.themeClass
      ];
    }
  }
});
