
import { defineComponent, PropType } from 'vue';

export default defineComponent({
  name: 'VToolbarLogo',
  props: {
    src: {
      type: String as PropType<string>,
      required: true
    },
    alt: {
      type: String as PropType<string>,
      default: null
    },
    to: {
      type: [Object, String],
      default: null
    },
    href: {
      type: String as PropType<string>,
      default: null
    },
    title: {
      type: String as PropType<string>,
      default: null
    }
  },
  emits: {
    click: null
  },
  computed: {
    component (): string {
      if (this.to) return 'RouterLink';
      if (this.href) return 'a';
      return 'span';
    }
  },
  methods: {
    onClick (): void {
      this.$emit('click');
    }
  }
});
