
import { defineComponent, PropType } from 'vue';
import { VSpinner } from '@/components/Spinner';
import { VIcon } from '@/components/Icon';
import { VButton } from '@/components/Button';

export default defineComponent({
  name: 'VAsyncContent',
  components: {
    VSpinner,
    VIcon,
    VButton
  },
  props: {
    loading: {
      type: Boolean as PropType<boolean>,
      required: true
    },
    spinnerSize: {
      type: String as PropType<'lg' | 'xs' | 'sm' | '1x' | '2x' | '3x' | '4x' | '5x' | '6x' | '7x' | '8x' | '9x' | '10x' | null>,
      default: '2x'
    },
    loadingText: {
      type: String as PropType<string>,
      default: ''
    },
    error: {
      type: Boolean as PropType<boolean>,
      default: false
    },
    errorText: {
      type: String as PropType<string>,
      default: ''
    },
    reloadable: {
      type: Boolean as PropType<boolean>,
      default: false
    },
    reloadText: {
      type: String as PropType<string>,
      default: ''
    }
  },
  emits: ['reload'],
  computed: {
    computedErrorText (): string {
      return this.errorText || this.$ui.t().asyncContent.errorText;
    },
    computedReloadText (): string {
      return this.reloadText || this.$ui.t().asyncContent.reloadText;
    }
  },
  methods: {
    reload (): void {
      this.$emit('reload');
    }
  }
});
